import React, { Component} from 'react';
// import { Link } from 'react-router-dom'
import {Base_URL,headers} from '../../CommonRoutes/Api'
import {
    CButton,
    CCard,
    CCardBody,
    CCol,
    CContainer,
    CInput,
    CInputGroup,
    CRow,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
// import Avtar from '../../assets/icons/logo'

const __DEV__ = document.domain === 'localhost'
class  Wallet extends Component {
  constructor(){
    super()
    this.state={
      withdraw:'',
      wallet:true,
      wallamt:false,
      amt:''
    }
  }
  
  loadScript(src) {
    return new Promise((resolve) => {
      const script = document.createElement('script')
      script.src = src
      script.onload = () => {
        resolve(true)
      }
      script.onerror = () => {
        resolve(false)
      }
      document.body.appendChild(script)
    })
  }

  	async displayRazorpay() {
      const self=this;

		const res =  await this.loadScript('https://checkout.razorpay.com/v1/checkout.js')

		if (!res) {
			alert('Razorpay SDK failed to load. Are you online?')
			return
		}

		// const data = await fetch('http://localhost:1337/razorpay', { method: 'POST' }).then((t) =>
		// 	t.json()
		// )

		// console.log(data)

		const options = {
			key: __DEV__ ? 'rzp_test_wOGiV1gpEDUQrb' : 'PRODUCTION_KEY',
			// currency: data.currency,
			// amount: data.amount.toString(),
			// order_id: data.id,
      // "amount": 50000,
      currency: "INR",
      amount:Number(self.state.amt+'00'),
			// name: 'Bada Bazarr',
			// description: 'Thank you for nothing. Please give us some money',
			image: 'https://scontent.famd4-1.fna.fbcdn.net/v/t1.6435-9/118735517_106259321210134_6609537456866208869_n.jpg?_nc_cat=108&ccb=1-3&_nc_sid=973b4a&_nc_ohc=OIXJOQyvfYAAX8g-uYI&_nc_ht=scontent.famd4-1.fna&oh=32147705a68745ef4c204efc1eb9c97e&oe=60D201C5',
			handler: function (response) {
        let url = Base_URL + "addtransaction";
        let transaction_id=response.razorpay_payment_id;
        let vendor_id=localStorage.getItem("user_id");
        let amount=Number(self.state.amt);
        let data={transaction_id,vendor_id,amount}
        fetch(url, { method: 'POST', headers, body: JSON.stringify(data)
        }).then((result) => {
        result.json().then(() => {
          // this.setState({modalIsOpen: false})
          // this.componentDidMount();
        },(error) => console.log(error))
      
        })
			},
			prefill: {
				email: 'sdfdsjfh2@ndsfdf.com',
				phone_number: '9899999999'
			}
		}
		const paymentObject = new window.Razorpay(options)
		paymentObject.open()
	}
    render() {
        return (
          <div>
          <CContainer style={{ padding: "10px" }}>
            <CRow className="justify-content-center">
              <CCol md="12" lg="12" xl="12" style={{ padding: "0px" }}>
                <CCard className="mx-10" style={{ backgroundColor: '#110730', marginLeft: '-1rem', marginRight: '-.9rem', marginBottom: '-.1rem' }}>
                  <CCardBody className="p-10"  >
                    <center style={{ color: "white" }}> <h4>Wallet</h4> </center>
  
                    <b><label style={{ color: "white" }}>Total</label></b>
                    <CInputGroup className="mb-3" >
  
                      <CInput type="text" placeholder="Rs. 0.00" autoComplete="false" name="total" value={this.state.total}
                        style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                    </CInputGroup>
  
  
                    <b> <label style={{ color: "white" }}>Add the Amount</label></b>
                    <CInputGroup className="mb-3" >
  
                      <CInput type="text" name="withdraw" placeholder="Please enter the amount to add to the wallet" autoComplete="false"
                        // value={this.state.withdraw}
                        onChange={(event) => { this.setState({ amt: event.target.value }) }}
                        style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                      />
                    </CInputGroup>
                    <span className="desc_heading" style={{ float: "right", marginTop: "-1rem", marginBottom: "0.4rem", color: "red", fontSize: "0.8rem" }}>The minimum adding amount is Rs. 100/-</span>
                  </CCardBody>
  
                </CCard>
                <CButton
                  type="submit"
                  value="add"
                  style={{ backgroundColor: 'orange', color: "black", marginTop: "1rem", borderRadius: '20px' }}
                  block color="primary"
                  // fullWidth={true}
                  onClick={() => this.displayRazorpay()}
                >ADD AMOUNT TO THE WALLET</CButton>
  
                <br />
                <p className="tran" style={{ textAlign: "center", color: "blueviolet", fontSize: "0.9rem", fontWeight: "bold" }}><u> Transaction</u> </p>
  
              </CCol>
            </CRow>
          </CContainer>
        </div>
        );
    }
}

export default Wallet;
